
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}


body {
    background-color: #e2e2e2;
}


button {
    padding: 15px 25px;
    font-size: 20px;
    color: white;
    background-color: #19cb98;
    border: 1px solid #19cb98;
    border-radius: 5px;
    cursor: pointer;
}


button:hover {
    background-color: #14a27a;
}


button:disabled {
    background-color: #e3e3e3;
    border: 1px solid #e3e3e3;
    color: grey;
}


.check-agreement {
    min-height: 40px !important;
}


.container {
    width: 100%;
    max-width: 1170px;
    margin: auto;
    padding: 10px 0 30px;
    background-color: white;
}


.correct-field {
    border: 2px solid #42b983 !important;
    color: black !important;
}


.delete-array-icon {
    color: red;
    font-size: 20px;
    cursor: pointer;
    position: absolute;
    margin: 12px -43px 0;
}


.disabled {
    display: none;
}


.empty-fields {
    display: inline-table;
    width: 33%;
    margin: 15px 0 0;
    min-height: 65px;
}


.form-field {
    width: 100%;
    max-width: 265px;
    height: 45px;
    font-size: 20px;
    padding-left: 10px;
    border: 2px solid black;
    border-radius: 5px;
}


.hidden {
    display: none;
}


#hidden-choise {  
    border-left: none !important; 
    border-top: none !important;
    border-bottom: none !important;
    border-top-right-radius: 0px !important;
    border-bottom-right-radius: 0px !important;
    outline: none;
    position: absolute;
    margin: 2px;
    width: 243px;
    height: 41px;
}


#hidden-choise:hover {
    cursor: default;
}


h2 {
    text-align: left;
    margin-left: 5%;
}


.incorrect-field {
    border: 2px solid red !important;
}


.incorrect-field-msg {
    font-size: 12px;
    color: red;
}


input {
    outline: none;
}


select {
    background: white;
}


select.form-field {
    color: grey;
    cursor: pointer;
}


select.form-field:focus {
    color: black;
}


.success-block {
    font-size: 30px;
    font-weight: 600;
    line-height: 50px;
}


@media (max-width: 910px) {

    .empty-fields {
        width: 45%;
    }

    h2 {
        margin-left: 11%;
    }

}


@media (max-width: 650px) {

    .empty-fields {
        width: 100%;
    }

    .form-field {
        max-width: 280px;
    }

    h2 {
        text-align: center;
        margin-left: 0;
    }

    #hidden-choise {
        width: 258px;
    }

}